////
/// DivoDivnoe Typ-ul
/// @group typ-ul
////

@include dd-system-componentRegister(dd-typ-ul, (dd-typ));

@mixin dd-typ-ul-generate() {
    ul {
        @include dd-typ-node(true) {
            list-style: disc;
            padding-left: 40px;

            ul,
            ol {
                margin-bottom: 0;
            }
        }
    }
}



