////
/// DivoDivnoe Typ-ol
/// @group typ-ol
////

@include dd-system-componentRegister(dd-typ-ol, (dd-typ));

@mixin dd-typ-ol-generate() {
    ol {
        @include dd-typ-node(true) {
            list-style: decimal;
            padding-left: 40px;

            ul,
            ol {
                margin-bottom: 0;
            }
        }
    }
}



