////
/// DivoDivnoe Typ-mark
/// @group typ-mark
////

@include dd-system-componentRegister(dd-typ-mark, (dd-typ));

@mixin dd-typ-mark-generate() {
    mark {
        @include dd-typ-node() {
            background: #eff6ff;
            padding: 0.2em;
        }
    }
}



