@include dd-system-componentRegister(dd-typ-abbr, (dd-typ));

@mixin dd-typ-abbr-generate() {
    abbr {
        @include dd-typ-node() {
            border-bottom: 1px dotted;
            cursor: help;
        }
    }
}



