////
/// DivoDivnoe Typ-a
/// @group typ-a
////

@include dd-system-componentRegister(dd-typ-a, (dd-typ));

@function dd-typ-a-bootstrap() {
    $dd-typ-a-color: #34a1cf !default !global;
    $dd-typ-a-colorHover: #cf3434 !default !global;
    $dd-typ-a-colorVisited: #9534cf !default !global;

    @return true;
}

@mixin dd-typ-a-generate() {
    a {
        @include dd-typ-node() {
            color: $dd-typ-a-color;
            text-decoration: underline;

            &:hover {
                color: $dd-typ-a-colorHover;
                text-decoration: none;
            }

            &:visited {
                color: $dd-typ-a-colorVisited;
            }
        }
    }
}



